<template>
    <div>
        <el-container>
            <el-header>
                <div class="home_title">
                    <el-row>
                        <el-col :span='18'>
                            <div class="logo">
                                <router-link to='/home/'>
                                    <img src="../../public/img/logo.png" alt="">
                                    <span>河北本制仪器仪表有限公司</span>
                                </router-link>
                            </div>
                        </el-col>
                        <el-col :span='6'>
                            <div class="setting">
                                <span style="font-size:16px;font-weight:bold;" @click='leave'><i class='el-icon-switch-button'></i></span>
                                <span>
                                    <i class="el-icon-user-solid"></i>
                                    {{realName}}
                                </span>
                            </div>
                        </el-col>
                    </el-row>
                </div>
            </el-header>
            <el-container>
                <el-aside width="200px" class='aside_l'>
                    <hd>
                        <div class="metu_list">
                            <el-menu :default-active="this.$route.path" router background-color='#373D41' text-color='#fff' @select="handleSelect" class="el-menu-horizontal-demo" @open="handleOpen" @close="handleClose">
                                <el-submenu v-for='(i,index) in list' :key='index' :index="i.path">
                                    <template slot="title">
                                        <i :class="i.iconfont"></i>
                                        <span slot='title'>{{i.title}}</span>
                                    </template>
                                    <el-menu-item-group v-for='(j,$index) in i.lists' :key='$index'>
                                        <el-menu-item :index='j.path'>{{j.title}}</el-menu-item>
                                    </el-menu-item-group>
                                </el-submenu>
                            </el-menu>
                        </div>
                    </hd>
                </el-aside>
                <el-container class='container_box'>
                    <hd>
                        <el-main>
                            <div class="main-content">
                                <router-view></router-view>
                            </div>
                        </el-main>
                        <el-footer>
                            <div class="footer">
                                <div class="name">
                                    <span>河北本制仪器仪表有限公司</span>
                                </div>
                                <div class="numbering">
                                    <span>© 2017 Hebeibenzhi 冀ICP备17020560号-1</span>
                                </div>
                            </div>
                        </el-footer>
                    </hd>
                </el-container>
            </el-container>
        </el-container>
    </div>
</template>
<script>
import hd from '../components/hdScroll.vue'
export default {
    components: {
        hd
    },
    data() {
        return {
            ai: '',
            userName: '',
            realName: '',
            activeIndex: '',
            list: [
                /*{
                    title: '首页',
                    path: '/home',
                    iconfont:'el-icon-house',
                    lists: [{
                        title: '首页',
                        path: '/home/'
                    }]}, */
                {
                    title: '公司',
                    path: '/company',
                    iconfont: 'el-icon-office-building',
                    lists: [{
                            title: '公司信息管理',
                            path: '/home/manage'
                        }
                        /* {
                                                title: '公司详情',
                                                path: '/home/company_details'
                                            }*/
                    ]
                }, {
                    title: '产品',
                    path: '/products',
                    iconfont: 'el-icon-box',
                    lists: [{
                        title: '新建产品',
                        path: '/home/add_product'
                    }, {
                        title: '产品管理',
                        path: '/home/product_manage'
                    }, {
                        title: '产品类型',
                        path: '/home/product_type'
                    }]
                }, {
                    title: '新闻',
                    path: '/news',
                    iconfont: 'el-icon-notebook-2',
                    lists: [{
                        title: '新建新闻',
                        path: '/home/add_news'
                    }, {
                        title: '新闻管理',
                        path: '/home/news_manage'
                    }, {
                        title: '新闻版块',
                        path: '/home/news_section'
                    }]
                }, {
                    title: '招聘',
                    path: '/recruitment',
                    iconfont: 'el-icon-date',
                    lists: [{
                        title: '新建岗位',
                        path: '/home/new_post'
                    }, {
                        title: '岗位管理',
                        path: '/home/post_manage'
                    }, {
                        title: '岗位类型',
                        path: '/home/post_type'
                    }]
                }, {
                    title: '系统',
                    path: '/system',
                    iconfont: 'el-icon-monitor',
                    lists: [{
                        title: '新建账号',
                        path: '/home/new_account'
                    },{
                        title: '账号管理',
                        path: '/home/account_manage'
                    }, {
                        title: '修改密码',
                        path: '/home/modify_password'
                    }, {
                        title: '操作日志',
                        path: '/home/operation_log'
                    }]
                }
            ]
        }
    },
    mounted() {
        this.userName = window.sessionStorage.getItem('userName')
        this.realName = window.sessionStorage.getItem('realName')
    },
    methods: {
        ext() {
            this.$router.push('/home/modify_password')
        },
        leave() {
            this.$confirm('确认退出, 是否继续?', '提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning'
            }).then((action) => {
                console.log(action)
                if (action === 'confirm') {
                    window.localStorage.clear();
                    window.sessionStorage.clear();
                    this.$router.push('/login');
                }
            }).catch(() => {
                this.$message({
                    type: 'info',
                    message: '已取消'
                });
            })
        },
        handleSelect(key, keyPath) {
            // console.log(key, keyPath);
        },
        handleOpen(key, keyPath) {
            // console.log(key, keyPath);
        },
        handleClose(key, keyPath) {
            // console.log(key, keyPath);
        }
    }
}
</script>
