.logo {
    width: 100%;
    height: 50px;
    background-color: #373D41;
}

.home_title {
    width: 100%;
    height: 50px;
    line-height: 50px;
    font-size: 18px;
    color: #fff;
    font-weight: bold;
    background-color: #373D41;
    z-index: 999;
}

.logo>a {
    color: #fff;
    text-decoration: none;
}

.logo>a>img {
    width: 50px;
    height: 50px;
    margin: 0 0px 0 17px;
    float: left;
}

.metu_list {
    width: 100%;
    height: auto;
    background: #373D41;
}

.main-content {
    width: 100%;
    height: auto;
    background: #f5f5f5;
}

.setting {
    width: 100%;
    height: 50px;
    /* background: #607379; */
}

.setting>span {
    font-size: 12px;
    float: right;
    margin: 0 30px 0 0px;
    cursor: pointer;
}

.setting>span>ul {
    background: #607379;
}
</style>
<style>
.aside_l {
    width: 100%;
    height: 100vh;
    margin-top: -50px;
    overflow: hidden;
    background: #373D41;
    padding-top: 50px;
}

.container_box {
    width: 100%;
    height: 100vh;
    margin-top: -50px;
    overflow: hidden;
    padding-top: 50px;
}

.el-container {
    overflow: hidden;
    background: #f5f5f5;
}

.el-header {
    padding: 0;
    height: 50px !important;
}

.el-main {
    padding: 0;
}

.el-footer {
    padding: 0;
    text-align: center;
    font-size: 12px;
    margin: 30px 0;
    line-height: 30px;
    color: #ccc;
    background: #f5f5f5;
}

.scrollbar {
    z-index: 99;
}

.el-menu-item.is-active {
    background-color: rgb(231, 235, 240) !important;
}

.el-menu-vertical-demo:not(.el-menu--collapse) {
    width: 100%;
    min-height: 400px;
}

.el-menu-item-group__title {
    display: none;
}

.el-submenu {
    min-width: 200px;
}
</style>